.pricing-page .cloud-box {
  height: 0;
  top: 200px;
}
.pricing-page .cloud-box .clouds {
  z-index: 0;
  width: 120%;
}
.pricing-page .title {
  padding: 100px 0;
}
.pricing-page .package {
  box-shadow: 0 10px 15px #ccc inset;
}
.pricing-page .feat-compare {
  box-shadow: 0 10px 15px #ccc inset;
}

.domain-page {
  position: relative;
  overflow: hidden;
}
.domain-page .clouds {
  z-index: 0;
  top: -160px;
}
.domain-page .domain {
  box-shadow: 0 10px 15px #ccc inset;
}
.domain-page .domain table .cross {
  color: #c62828;
}
.domain-page .package {
  box-shadow: 0 10px 15px #ccc inset;
}
.domain-page .feature-compare {
  box-shadow: 0 10px 15px #ccc inset;
}
.domain-page .contact {
  box-shadow: 0 10px 15px #ccc inset;
}

.blog-page {
  position: relative;
  overflow: hidden;
}
.blog-page .clouds {
  z-index: 0;
  top: -160px;
}
.blog-page .content {
  box-shadow: 0 10px 15px #ccc inset;
}

.contact-page {
  position: relative;
  overflow: hidden;
}
.contact-page .clouds {
  z-index: 0;
  top: -160px;
}
.contact-page .office-info {
  box-shadow: 0 10px 15px #ccc inset;
}
.contact-page .office-info .box svg {
  padding: 20px;
  color: #fff;
  background-color: #3ab3e7;
  border-radius: 50%;
  margin-bottom: 20px;
}
.contact-page .office-info .box h5 {
  color: #1e455b;
  font-weight: bold;
  margin-bottom: 15px;
}
.contact-page .office-info .box address {
  margin-bottom: 8px;
  color: #637988;
  font-size: 16px;
}
.contact-page .find-us {
  box-shadow: 0 10px 15px #ccc inset;
}
.contact-page .find-us .img-box {
  position: relative;
  min-height: 500px;
  background-image: url("/dist/imgs/company.jpg");
  background-size: cover;
}
.contact-page .find-us .img-box .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.contact-page .mail-us {
  box-shadow: 0 10px 15px #ccc inset;
}
.contact-page .mail-us form label {
  color: #1e455b;
  margin-bottom: 10px;
  padding-left: 10px;
}
.contact-page .mail-us form label span {
  color: #3ab3e7;
}
.contact-page .mail-us form input {
  width: 100%;
  padding: 10px 15px;
  border: 2px solid #ddd;
  border-radius: 10px;
  margin-bottom: 30px;
}
.contact-page .mail-us form input:focus {
  outline: none;
}
.contact-page .mail-us form input::placeholder {
  color: #637988;
}
.contact-page .mail-us form textarea {
  width: 100%;
  padding: 10px 15px;
  min-height: 300px;
  border: 2px solid #ddd;
  border-radius: 10px;
  resize: none;
}
.contact-page .mail-us form textarea:focus {
  outline: none;
}
.contact-page .mail-us form textarea::placeholder {
  color: #637988;
}